<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Identity Verification</p>
        </div>
        <div class="orderDetailWrap">
            <div class="failedBox" v-if="failedArr.length > 0 && displayIdentityData.status == 'failed'">
                <p>There is a problem with the information you submitted.<br>The {{failedArr[0]}} is incorrect.</p>
            </div>
            <div class="failedBox" v-else-if="failedArr.length == 0 && displayIdentityData.status == 'failed'">
                <p>There is a problem with the information you submitted.<br>There is a risk of cheating on your account.</p>
            </div>
            <div class="withdrawalContent" v-if="stepNum == 1">
                <div class="withdrawalInputWrap">
                    <div class="withdrawalInputItem">
                        <p class='InputTitle'>Nationality:</p>
                        <div class="inputOuterBox flexBetween" @click="showNationality = true">
                            <input class='withdrawalInput' disabled v-model="selectedNationalityData.name"/>
                        </div>
                    </div>
                    <div class="withdrawalInputItem flexBetween">
                        <div class="NameItemBox" style="width: 3.17rem;margin-right: .25rem;position: relative;">
                            <p class='InputTitle'>First Name:</p>
                            <div class="inputOuterBox flexBetween" :class="firstNameRedBorder ? 'RedBorder' : ''">
                                <input class='withdrawalInput' v-model="submitIdentityData.firstName" maxlength="30" @input.prevent="blurFirst_name(submitIdentityData.firstName)"/>
                                <img class="clearNumberIcon" v-if="submitIdentityData.firstName" @click="submitIdentityData.firstName = '';blurFirst_name(submitIdentityData.firstName)" src="../assets/images/clearInputIcon.png" alt="">
                            </div>
                            <p v-if="firstNameAlert" class='withdrawalAlertText'>{{firstAlertText}}</p>
                        </div>
                        <div class="NameItemBox" style="width: 3.17rem;">
                            <p class='InputTitle'>Last Name:</p>
                            <div class="inputOuterBox flexBetween" :class="lastNameRedBorder ? 'RedBorder' : ''">
                                <input class='withdrawalInput' v-model="submitIdentityData.lastName" maxlength="30" @input.prevent="blurLast_name(submitIdentityData.lastName)"/>
                                <img class="clearNumberIcon" v-if="submitIdentityData.lastName" @click="submitIdentityData.lastName = '';blurLast_name(submitIdentityData.lastName)" src="../assets/images/clearInputIcon.png" alt="">
                            </div>
                            <p v-if="lastNameAlert" class='withdrawalAlertText'>{{lastNameAlertText}}</p>
                        </div>
                    </div>
                    <div class="withdrawalInputItem">
                        <p class='InputTitle'>Date of Birth:</p>
                        <div class="inputOuterBox flexBetween" :class="birthRedBorder ? 'RedBorder' : ''">
                            <input class='withdrawalInput' v-model="submitIdentityData.dateOfBirth" type="date" @change.prevent="blurdateOfBirth(submitIdentityData.dateOfBirth)"/>
                            <!-- <mt-field class='normalInput'  placeholder="" type="date"></mt-field> -->
                            <!-- <img class="clearNumberIcon" src="../assets/mainImages/dateIcon.png" alt=""> -->
                        </div>
                        <p v-if="dateOfBirthAlert" class='withdrawalAlertText'>{{dateOfBirthText}}</p>
                    </div>
                </div>
                <!-- <mt-button :disabled='!allCheck'  @click="goStep(2)" class='coutinueBtn withdrawalContinue' :class="allCheck ? 'allowClick' : 'clickNotAllowed'" >Continue</mt-button> -->
                    <div class="submitBtn " :class="allCheck ? 'GradientAllowBtn':''" style="margin-top: .45rem;margin-bottom: .2rem;" @click="allCheck ? goStep(2) : ''">Next</div>
            </div>
            <div class="withdrawalContent" v-else-if="stepNum == 2">
                <div class="withdrawalInputWrap">
                    <div class="withdrawalInputItem">
                        <p class='InputTitle'>Document Type:</p>
                        <div class="inputOuterBox flexBetween" @click="showDocumentTypeList = true">
                            <input class='withdrawalInput' disabled @change='checkAll2()' v-model="selectedDocumentType.name"/>
                            <!-- <img class="cellRightArrowIcon" src="../assets/mainImages/cellRightArrowIcon.png" alt=""> -->
                        </div>
                        <p v-if="documentTypeAlert" class='withdrawalAlertText'>Can not be empty</p>
                    </div>
                    <div class="withdrawalInputItem">
                        <p class='InputTitle'>Document Number:</p>
                        <div class="inputOuterBox flexBetween" :class="documentNumberRedBorder ? 'RedBorder' : ''">
                            <input class='withdrawalInput' v-model="submitIdentityData.documentNumber" maxlength="30" @input.prevent="blurDocumentNumber(submitIdentityData.documentNumber)"/>
                            <img class="clearNumberIcon" v-if="submitIdentityData.documentNumber" @click="submitIdentityData.documentNumber = '';blurDocumentNumber(submitIdentityData.documentNumber)" src="../assets/images/clearInputIcon.png" alt="">
                        </div>
                        <p v-if="documentNumberAlert" class='withdrawalAlertText'>{{documentNumberText}}</p>
                    </div>
                    <div class="withdrawalInputItem" style="margin-bottom: 0;">
                        <p class='InputTitle'>Upload a photo of the ID:</p>
                        <div class="taskPictureBox">
                            <div class="taskPictureItem" @click='chooseImgBtn(proofList.isCanUpdate, proofList.image)' :class="documentNumberRedBorder ? 'RedBorder' : ''">
                                <template v-if="proofList.isCanUpdate">
                                    <div class="cameraBox"  v-if="proofList.isUploading == 'normal'">
                                        <img class='cameraIcon' src="../assets/images/cameraIcon.png" alt="">
                                        <p>Choose the page with your photo</p>
                                        <input type="file" class='uploadInput' id="choose" accept="image/*">
                                    </div>
                                    <div class="cameraBox" v-else-if='proofList.isUploading == "uploading"'>
                                        <img class='cameraIcon loadingTransform' style="width:.54rem;height:.54rem;" src="../assets/images/videoLoading.png" alt="">
                                        <p>Uploading...</p>
                                    </div>
                                    <div class="cameraBox" v-else-if='proofList.isUploading == "error"'>
                                        <img class='cameraIcon' style="width:.54rem;height:.54rem;" src="../assets/images/errorIcon.png" alt="">
                                        <p style="color: #FF274C;">Failed</p>
                                        <input type="file" class='uploadInput' id="choose" accept="image/*">
                                    </div>
                                </template>
                                <template v-else>
                                    <img class='deleteImgIcon' src="../assets/images/uploadrechange.png" alt="" @click.stop="deleteProofImg()">
                                    <img class='taskPicture' :src="proofList.image" alt="">
                                </template>
                            </div>
                            <img class='idCardIcon' src="../assets/images/indenIDCard.png" alt="">
                        </div>
                        <p v-if="PhotoOfIdAlert" style="left: 0;right: inherit;" class='withdrawalAlertText'>{{PhotoOfIdText}}</p>
                    </div>
                    <p class='photoScriptText'>Information in the document, especially the ID No. and the name,shall be clear and visible without any modification or cover.</p>
                </div>
                <!-- <mt-button @click="submitIdentity()" class='coutinueBtn withdrawalContinue' :class="allCheck2 ? 'allowClick' : 'clickNotAllowed'" >Submit</mt-button> -->
            </div>
            <div class="withdrawalContent" v-else-if="stepNum == 3">
                <template v-if="displayIdentityData.status == 'unverified'">
                    <!-- <img class='identityReviewIcon' src="../assets/mainImages/identityReview.png" alt=""> -->
                    <p class='identityReviewText'>Your application has been submitted<br>Please wait for review</p>
                </template>
                <template v-else-if="displayIdentityData.status == 'verified'">
                    <!-- <img class='identityReviewIcon' src="../assets/mainImages/identitySuccess.png" alt=""> -->
                    <p class='identityReviewText'>Your identity has been verifed<br>Related features are enabled</p>
                </template>
                <div class="identityMsgReviewBox">
                    <div class="identityMsgReviewItem flexBetween">
                        <span class="reviewLeft">Last Name</span>
                        <span class="reviewRight">{{displayIdentityData.submit.lastName}}</span>
                    </div>
                    <div class="identityMsgReviewItem flexBetween">
                        <span class="reviewLeft">Document Type</span>
                        <span class="reviewRight">{{reviewDocumentType}}</span>
                    </div>
                    <div class="identityMsgReviewItem flexBetween">
                        <span class="reviewLeft">Document Number</span>
                        <span class="reviewRight">{{reviewDocumentNumber}}</span>
                    </div>
                </div>
            </div>
        </div>
        <van-popup v-model="showNationality" position="bottom">
            <div class="countrySelectBox">
                <div class="countryTitBox">
                    <p class='countryTit'>Select nationality</p>
                    <img @click='showNationality = !showNationality' style="width: .29rem;height: .29rem;" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="mainSelectBox" v-if="displayIdentityData && displayIdentityData.nationalityList">
                    <div v-for="(item,index) in displayIdentityData.nationalityList" :key="index" class='countryCell' @click="chooseBank(item)" :class="item.code == selectedNationalityData.code ? 'blueBg' : ''">
                        <div class="countryItemLeft">
                            <span class='countryName'>{{item.name}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
	import https from '../api/https'
    import { FormDataShim, getBlob} from '../assets/js/compress';
	export default {
		name: 'IdentityVerification',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                
                icUrl: https.ImgUploadUrl,
                bigImg: '',
                proofList: {
                    isCanUpdate: true,
                    isUploading: 'normal',
                    image: null,
                },
                showBigImgBox: false,
                // 
                showCaptchaBox: false,
                //   --------------------------
                firstNameRedBorder: false,
                lastNameRedBorder: false,
                birthRedBorder: false,
                documentNumberRedBorder: false,
                IDPhotoRedBorder: false,
                failedArr: [],
                displayIdentityData: {},
                submitIdentityData: {},
                stepNum: 1,
                showDocumentTypeList: false,
                documentTypeList: [
                    {
                        name: 'Passport',
                        id: 'passport'
                    },
                    {
                        name: "Driver's license",
                        id: 'driving_license'
                    },
                    {
                        name: 'ID card or other documents',
                        id: 'identity_card'
                    },
                ],
                reviewDocumentType: '',
                reviewDocumentNumber: '',
                selectedNationalityData: {
                    code: '',
                    name: '',
                },
                selectedDocumentType: {
                    name: '',
                    id: '',
                },
                showNationality: false,
                
                // 
                firstNameAlert: false,
                firstAlertText: '',

                lastNameAlert: false,
                lastNameAlertText: '',

                dateOfBirthAlert: false,
                dateOfBirthText: '',

                documentNumberAlert: false,
                documentNumberText: '',

                PhotoOfIdAlert: false,
                PhotoOfIdText: '',

                documentTypeAlert: false,

                firstNameCheck: true,
                lastNameCcheck: true,
                dateOfBirthCheck: true,
                documentNumberCheck: true,
                PhotoOfIdCheck: true,
                // emailCheck: true,
                allCheck: false,
                allCheck2: false,
			}
		},
		created(){
            // this.getIdentityMsg()
			// this.getData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code')) {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/user/account' ).then((data) => {
					this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.accountData = res.data
                        
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
					console.log(data)
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},

            submitIdentity(){
                if(!this.allCheck2) {
                    return
                }
                if(this.displayIdentityData.status == 'nodata') {
                    console.log(this.proofList.image)
                    if(!this.proofList.image) {
                        this.PhotoOfIdAlert = true
                        this.PhotoOfIdText = 'Can not be empty'
                        return
                    }
                }
                this.$toast.loading({
                    message: 'Loading...',
                    forbidClick: true,
                    loadingType: 'spinner',
                    icon: require('../assets/images/loadingIcon.png'),
                    duration: 0,
                    className:'toastLoading'
                });
                
                let params = this.submitIdentityData
                if(this.proofList.image) {
                    params.photoOfId = this.proofList.image
                    delete params.photoOfHoldingId
                }else {
                    delete params.photoOfId
                    delete params.photoOfHoldingId
                }
                if(this.selectedNationalityData) {
                    params.nationality = this.selectedNationalityData.code
                }
                if(this.selectedDocumentType) {
                    params.documentType = this.selectedDocumentType.id
                }
                
                https.fetchPost('/api/user/verification/submit',params).then((data) => {
                    this.MyToastActive = false
                    let res = data.data
                    this.showLoading = false
                    if(res.code == 0) {
                        this.$toast({
							message: 'Successfully submitted',
							forbidClick: true,
							icon: require('../assets/images/successIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
                        this.getIdentityMsg()
                    }else {
                        this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
                    }
                }).catch(err=>{
                    console.log(err)
                    if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
                        this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
                        this.NetWorkRetryTimes = 0
                    }else {
                        this.NetWorkRetryTimes = 0
                        this.NetWorkRetryTimes++
                        this.submitIdentity()
                    }
                })
            },
            getIdentityMsg(){
                https.fetchGet('/api/user/verification/status').then((data) => {
                    let res = data.data
                    if(res.code == 0) {
                        this.displayIdentityData = res.data
                        if(res.data.submit) {
                            this.submitIdentityData = res.data.submit
                            for (let index = 0; index < this.displayIdentityData.nationalityList.length; index++) {
                                if(res.data.submit.nationality == this.displayIdentityData.nationalityList[index].code) {
                                    this.selectedNationalityData = this.displayIdentityData.nationalityList[index]
                                }
                            }
                            for (let index = 0; index < this.documentTypeList.length; index++) {
                                if(res.data.submit.documentType == this.documentTypeList[index].id) {
                                    this.selectedDocumentType = this.documentTypeList[index]
                                }
                            }
                        }
                        if(this.displayIdentityData.status == 'nodate') {
                            this.stepNum = 1
                        }else if(this.displayIdentityData.status == 'unverified' || this.displayIdentityData.status == 'verified') {
                            this.stepNum = 3
                            for (let index = 0; index < this.documentTypeList.length; index++) {
                                if(this.documentTypeList[index].id == this.displayIdentityData.submit.documentType) {
                                    this.reviewDocumentType = this.documentTypeList[index].name
                                }
                            }
                            let starLength = this.displayIdentityData.submit.documentNumber.length - 4
                            let newStrStart = this.displayIdentityData.submit.documentNumber.slice(0,2)
                            let newStrEnd = this.displayIdentityData.submit.documentNumber.slice(-2)
                            let starStr = ''
                            for (let index = 0; index < starLength; index++) {
                                starStr = starStr+'*'
                            }
                            this.reviewDocumentNumber = newStrStart + starStr + newStrEnd
                        }else if(this.displayIdentityData.status == 'failed') {
                            this.stepNum = 0
                            this.allCheck = true
                            this.allCheck2 = true
                            if(this.displayIdentityData.failed.length > 0) {
                                for (let index = 0; index < this.displayIdentityData.failed.length; index++) {
                                    if(this.displayIdentityData.failed[index] == 'firstName') {
                                        this.failedArr[index] = 'first name'
                                        this.firstNameRedBorder = true
                                        this.stepNum = 1
                                    }else if(this.displayIdentityData.failed[index] == 'lastName') {
                                        this.failedArr[index] = 'last name'
                                        this.lastNameRedBorder = true
                                        this.stepNum = 1
                                    }else if(this.displayIdentityData.failed[index] == 'dateOfBirth') {
                                        this.failedArr[index] = 'date of birth'
                                        this.birthRedBorder = true
                                        this.stepNum = 1
                                    }else if(this.displayIdentityData.failed[index] == 'documentNumber') {
                                        this.failedArr[index] = 'document number'
                                        this.documentNumberRedBorder = true
                                        if(this.stepNum != 1) this.stepNum = 2
                                    }else if(this.displayIdentityData.failed[index] == 'photoOfId' || this.displayIdentityData.failed[index] == 'photo_of_id') {
                                        this.failedArr[index] = 'photo of the ID'
                                        this.IDPhotoRedBorder = true
                                        if(this.stepNum != 1) this.stepNum = 2
                                    }else {
                                        this.failedArr.push(this.displayIdentityData.failed[index])
                                        this.stepNum = 1
                                    }
                                }
                            }else {
                                this.stepNum = 1
                            }
                        }
                    }else {
                        this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
                    }
                }).catch(err=>{
                    console.log(err)
                    if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
                        this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
                        this.NetWorkRetryTimes = 0
                    }else {
                        this.NetWorkRetryTimes = 0
                        this.NetWorkRetryTimes++
                        this.getIdentityMsg()
                    }
                })
            },
            chooseBank: function(item){
                this.selectedNationalityData= item;
                this.submitIdentityData.nationality = item.name
                this.showNationality = false;
                this.checkAll()
            },
            chooseDocumentType(item){
                this.selectedDocumentType = item
                this.showDocumentTypeList = false;
                this.documentTypeAlert = false
                this.checkAll2()
            },
            // blur
            blurFirst_name(name){
                let rge = /[<'>%*`?/&$]/g;
                if(!name || name.length < 1) {
                    this.firstNameAlert = true
                    this.firstAlertText = 'Can not be empty'
                    this.firstNameCheck = false
                }else if(rge.test(name)){
                    this.firstNameAlert = true
                    this.firstAlertText = 'Invalid first name'
                    this.firstNameCheck = false
                }else {
                    this.firstNameAlert = false
                    this.firstAlertText = ''
                    this.firstNameCheck = true
                }
                this.checkAll()
            },
            blurLast_name(name){
                let rge = /[<'>%*`?/&$]/g;
                
                if(!name || name.length < 1) {
                    this.lastNameAlert = true
                    this.lastNameAlertText = 'Can not be empty'
                    this.lastNameCcheck = false
                }else if(rge.test(name)){
                    this.lastNameAlert = true
                    this.lastNameAlertText = 'Invalid last name'
                    this.lastNameCcheck = false
                }else {
                    this.lastNameAlert = false
                    this.lastNameAlertText = ''
                    this.lastNameCcheck = true
                }
                this.checkAll()
            },
            blurdateOfBirth(dateOfBirth) {
                if(!dateOfBirth) {
                    this.dateOfBirthAlert = true
                    this.dateOfBirthText = 'Can not be empty'
                    this.dateOfBirthCheck = false
                }
                this.checkAll()
            },
            blurDocumentNumber(documentNumber){
                let rge = /[<'>%*`?/&$]/g;
                if(!documentNumber || documentNumber.length < 5) {
                    this.documentNumberAlert = true
                    this.documentNumberText = 'At least 5 characters'
                    this.documentNumberCheck = false
                }else if(rge.test(documentNumber)){
                    this.documentNumberAlert = true
                    this.documentNumberText = 'Invalid first name'
                    this.documentNumberCheck = false
                }else {
                    this.documentNumberAlert = false
                    this.documentNumberText = ''
                    this.documentNumberCheck = true
                }
                this.checkAll2()
            },
            checkPhotoOfId(){
                if(this.proofList.image) {
                    this.PhotoOfIdCheck = true
                }else {
                    this.PhotoOfIdCheck = false
                    this.PhotoOfIdAlert = true
                    this.PhotoOfIdText = 'Can not be empty'
                }
            },
            checkAll(){
                if((!this.submitIdentityData.firstName || !this.firstNameCheck) || (!this.submitIdentityData.lastName || !this.lastNameCcheck) || !this.submitIdentityData.dateOfBirth || !this.selectedNationalityData.code ) {
                    this.allCheck = false
                    return
                }else {
                    this.allCheck = true
                }
            },
            checkAll2(){
                if((!this.submitIdentityData.documentNumber || !this.documentNumberCheck) || !this.selectedDocumentType.id) {
                    this.allCheck2 = false
                    return
                }else {
                    this.allCheck2 = true
                }
            },
            goStep(stepNum) {
                this.stepNum = stepNum
            },
            // 
            deleteProofImg(){
                    this.proofList.isCanUpdate = true
                    this.proofList.image = ''
                    // this.bigImg = this.bigImg+ '/' //LZY: Another way to update the view
            },
            chooseImgBtn(isCanUpdate,bigImgSrc) {
                console.log('isCanUpdate',isCanUpdate)
                if(!isCanUpdate) {
                    this.goshowBigImg(bigImgSrc)
                    return
                }else {
                    this.goUploadImg()
                }
            },
            goUploadImg() {
                var vm = this
                var filechooser = document.getElementById("choose");
                console.log('vm.canvas',vm.canvas)
                console.log('vm.ctx',vm.ctx)
                console.log('vm.tCanvas',vm.tCanvas)
                console.log('vm.tctx',vm.tctx)
                vm.canvas = vm.canvas || document.createElement("canvas");
                vm.ctx = vm.ctx || vm.canvas.getContext('2d');
                vm.tCanvas = vm.tCanvas || document.createElement("canvas");
                vm.tctx = vm.tctx || vm.tCanvas.getContext("2d");
                var maxsize = 3141593;
                filechooser.onchange = function() {
                    vm.proofList.isUploading = 'uploading'
                    vm.bigImg = vm.bigImg + '/' //LZY: Another way to update the view

                    // if (!this.files.length) return;
                    var files = Array.prototype.slice.call(this.files);
                    files.forEach(function(file) {
                        if (!/\/(?:jpeg|png|gif)/i.test(file.type)) return;
                        var reader = new FileReader();
                        reader.onload = function() {
                            var result = this.result;
                            var img = new Image();
                            img.src = result;
                
                            if (result.length <= maxsize) {
                                img = null;
                                vm.upload(result, file.type,file.name);
                                return;
                            }
                            if (img.complete) {
                                callback();
                            } else {
                                img.onload = callback;
                            }
                            function callback() {
                                var data = vm.compress(img);
                                vm.upload(data, file.type,file.name);
                                img = null;
                            }
                        };
                        reader.readAsDataURL(file);
                    })
                }
            },
            compress(img) {
                const vm = this
                var initSize = img.src.length;
                var width = img.width;
                var height = img.height;
                
                var ratio;
                if ((ratio = width * height / 3145782) > 1) {
                    ratio = Math.sqrt(ratio);
                    width /= ratio;
                    height /= ratio;
                } else {
                    ratio = 1;
                }
                vm.canvas.width = width;
                vm.canvas.height = height;
                
                vm.ctx.fillStyle = "#fff";
                vm.ctx.fillRect(0, 0, vm.canvas.width, vm.canvas.height);

                var count;
                if ((count = width * height / 1000000) > 1) {
                    count = ~~(Math.sqrt(count) + 1);
                
                    var nw = ~~(width / count);
                    var nh = ~~(height / count);
                    vm.tCanvas.width = nw;
                    vm.tCanvas.height = nh;
                    for (var i = 0; i < count; i++) {
                        for (var j = 0; j < count; j++) {
                            vm.tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
                            vm.ctx.drawImage(vm.tCanvas, i * nw, j * nh, nw, nh);
                        }
                    }
                } else {
                    vm.ctx.drawImage(img, 0, 0, width, height);
                }
                
                var ndata = vm.canvas.toDataURL('image/jpeg', 0.1);
                console.log('Before compression' + initSize);
                console.log('After compression' + ndata.length);
                console.log('Compression ratio' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
                vm.tCanvas.width = vm.tCanvas.height = vm.canvas.width = vm.canvas.height = 0;
                return ndata;
            },
            upload(basestr, type,fileName) {
                console.log(fileName)
                const vm = this
                var text = window.atob(basestr.split(",")[1]);
                var buffer = new Uint8Array(text.length);
                for (var i = 0; i < text.length; i++) {
                    buffer[i] = text.charCodeAt(i);
                }
                var blob = getBlob([buffer], type);
                var xhr = new XMLHttpRequest();
                var formdata = this.getFormData();
                formdata.append('file', blob);
                formdata.append('type', 'photo');
                formdata.append('name', fileName);
                xhr.open('post', this.picUrl);
                xhr.setRequestHeader("user-token", localStorage.getItem('user-token'));
                xhr.withCredentials = false;
                xhr.send(formdata);
                xhr.onreadystatechange = function() {
                    if (xhr.readyState == 4 && xhr.status == 200) {
                        var jsonData = JSON.parse(xhr.responseText);
                        var imagedata = jsonData || {};
                        if(imagedata.code == 0) {
                            vm.proofList.image = imagedata.data.url
                            vm.proofList.isCanUpdate = false
                            vm.proofList.isUploading = 'normal'
                            vm.bigImg = vm.bigImg + '/' //LZY: Another way to update the view
                            vm.canvas = null
                            vm.ctx = null
                            vm.tCanvas = null
                            vm.tctx = null
                        }else if(imagedata.code == -3 || imagedata.code == -2) {
                            vm.$router.push('/login')
                        } else {
                            this.$toast({
                                message: 'upload failed',
                                forbidClick: true,
                                duration: 2000,
                                className: 'smallToast'
                            });
                            vm.proofList.isUploading = 'error'
                            vm.bigImg = vm.bigImg + '/' //LZY: Another way to update the view
                        }
                    }
                }
            },
            goshowBigImg(bigImgSrc){
                this.bigImg = bigImgSrc
                this.showBigImgBox = true
            },
            getFormData() {
                var isNeedShim = ~navigator.userAgent.indexOf('Android')
                    && ~navigator.vendor.indexOf('Google')
                    && !~navigator.userAgent.indexOf('Chrome')
                    && navigator.userAgent.match(/AppleWebKit\/(\d+)/).pop() <= 534;
                return isNeedShim ? new FormDataShim() : new FormData()
            },
		}
	}
</script>
